@font-face {
  font-family: "twaysky";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_tway@1.0/twaysky.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Pretendard-Regular";
  src: url("https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
section {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 1400px;
  opacity: 1;
  position: relative;
  width: 100%;
}

* {
  margin: 0;
  padding: 0;
}

body {
  word-break: keep-all;
}

.gnb {
  align-items: center;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  background-color: hsla(0, 0%, 100%, 0.466);
  border-bottom: 1px solid #ccc;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  height: 86px;
  left: 0;
  padding: 10px 20px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}
.gnb .logo_img {
  width: 9%;
  margin-left: 1%;
  cursor: pointer;
}
.gnb .logo_img img {
  width: 100%;
}
.gnb .menu {
  display: flex;
}
.gnb .menu div {
  width: 100px;
  cursor: pointer;
}
.gnb .menu div span {
  color: rgb(0, 119, 255);
}
.gnb .menu div p {
  font-weight: 600;
}

.Main {
  display: flex;
  align-items: center;
  height: calc(var(--vh, 1vh) * 100);
  background: url("../img/Main_img.png") no-repeat;
  background-size: cover;
  position: relative;
}
.Main .Main_text {
  overflow: hidden;
  padding-left: 50px;
  letter-spacing: 6px;
}
.Main .Main_text span {
  font-family: "twaysky";
  font-size: 200px;
  font-weight: 600;
}
.Main .Main_text p {
  font-family: "twaysky";
  font-size: 200px;
  font-weight: 600;
  color: #fff;
  text-shadow: -1px -1px 0 #ccc, 1px -1px 0 #ccc, -1px 1px 0 #ccc, 1px 1px 0 #ccc;
}
.Main .Mouse_scroll {
  position: absolute;
  z-index: 99;
  bottom: 50px;
  left: 50%;
  background-size: cover;
  transform: translateX(-50%);
  color: #ccc;
  font-size: 14px;
  text-align: center;
  animation: moveUpdown 2s ease-in-out infinite;
}
.Main .Mouse_scroll .Mouse_img {
  width: 50px;
  height: 70px;
  background: url("../img/mouse.png") no-repeat;
  background-size: cover;
}
@keyframes moveUpdown {
  0% {
    bottom: 50px;
  }
  50% {
    bottom: 70px;
  }
  100% {
    bottom: 50px;
  }
}

.About {
  width: 100%;
  height: calc(var(--vh, 1vh) * 30);
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.About .About_menu {
  width: 100%;
  display: flex;
  justify-content: center;
}
.About .About_menu div {
  margin: 10px;
  padding: 20px;
}
.About .About_menu div span {
  font-family: "twaysky";
  font-size: 50px;
  font-weight: 600;
  color: #fff;
  transition: all 0.3s;
  text-shadow: -1px -1px 0 #979797, 1px -1px 0 #979797, -1px 1px 0 #979797, 1px 1px 0 #979797;
}
.About .About_menu div p {
  font-size: 25px;
  font-weight: 600;
  font-family: "Pretendard-Regular";
}
.About .About_menu div ul {
  margin-top: 10px;
  font-family: "Pretendard-Regular";
}
.About .About_menu div ul li {
  font-size: 14px;
  letter-spacing: 1px;
  margin-bottom: 5px;
}
.About .About_menu div ul li a {
  color: #000;
}
.About .About_menu div ul li a:hover {
  color: rgb(0, 119, 255);
}
.About .About_menu div:hover span {
  font-family: "twaysky";
  font-size: 50px;
  font-weight: 600;
  color: black;
  text-shadow: -1px -1px 0 #000000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.AboutMe {
  padding: 200px 0 200px;
  display: flex;
  flex-wrap: wrap;
}
.AboutMe .AboutMe_title {
  width: 100%;
  font-size: 100px;
  font-family: "twaysky";
}
.AboutMe .AboutMe_area {
  width: 50%;
  font-family: "Pretendard-Regular";
  margin-left: 30px;
  margin-top: 30px;
}
.AboutMe .AboutMe_area ul {
  display: flex;
}
.AboutMe .AboutMe_area ul li {
  font-size: 25px;
  list-style: none;
}
.AboutMe .AboutMe_area ul li span {
  font-weight: 600;
  margin-right: 10px;
}
.AboutMe .AboutMe_area p {
  margin-top: 10px;
  width: 500px;
  line-height: 25px;
}
.AboutMe .AboutMe_area .AboutMe_list {
  margin-top: 30px;
}
.AboutMe .AboutMe_area .AboutMe_list .AboutMe_list_item {
  margin-bottom: 20px;
}
.AboutMe .AboutMe_area .AboutMe_list .AboutMe_list_item .list_time {
  font-size: 17px;
  color: rgb(0, 119, 255);
}
.AboutMe .AboutMe_area .AboutMe_list .AboutMe_list_item .list_title {
  margin-top: 5px;
  font-size: 20px;
}
.AboutMe .AboutMe_area .AboutMe_list .AboutMe_list_item .list_title span {
  font-size: 15px;
  color: #979797;
}
.AboutMe .AboutMe_skils {
  width: 40%;
}
.AboutMe .AboutMe_skils .satellites {
  margin-top: 50px;
  animation: rotate-axis 10s linear infinite;
  transform-style: preserve-3d;
  display: flex;
  justify-content: center;
}
@keyframes rotate-axis {
  from {
    transform: perspective(800px) rotateY(0deg);
  }
  to {
    transform: perspective(800px) rotateY(360deg);
  }
}
.AboutMe .AboutMe_skils .satellites .satellite {
  position: absolute;
  width: 200px;
  transform: rotateY(calc(var(--i) * 1deg)) translateZ(100px);
  color: #979797;
  font-size: 20px;
  font-family: "twaysky";
  transition: all 0.3s;
}
.AboutMe .AboutMe_skils .satellites .satellite:hover {
  color: rgb(0, 119, 255);
}
.AboutMe .AboutMe_skils .satellites .satellite:nth-child(1) {
  transform: rotateY(0deg) translateZ(150px);
}
.AboutMe .AboutMe_skils .satellites .satellite:nth-child(2) {
  transform: rotateY(100deg) translateZ(150px);
}
.AboutMe .AboutMe_skils .satellites .satellite:nth-child(3) {
  transform: rotateY(200deg) translateZ(150px);
}
.AboutMe .AboutMe_skils .satellites .satellite:nth-child(4) {
  transform: rotateY(300deg) translateZ(150px);
}
.AboutMe .AboutMe_skils .satellites.sub {
  margin-top: 50px;
}
.AboutMe .AboutMe_skils .AboutMe_list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 100px;
}
.AboutMe .AboutMe_skils .AboutMe_list div {
  width: 100%;
  margin-bottom: 20px;
}
.AboutMe .AboutMe_skils .AboutMe_list div p {
  margin-right: 30px;
  font-size: 25px;
  font-family: "twaysky";
}
.AboutMe .AboutMe_skils .AboutMe_list div ul {
  margin-top: 10px;
}
.AboutMe .AboutMe_skils .AboutMe_list div ul li {
  margin: 5px 0;
  font-family: "Pretendard-Regular";
}

.Work {
  height: calc(var(--vh, 1vh) * 100);
  padding: 200px 0 200px;
  display: flex;
  flex-wrap: wrap;
}
.Work .Work_title {
  margin-top: 100px;
  width: 100%;
  height: 120px;
  font-size: 100px;
  font-family: "twaysky";
}
.Work .Work_list {
  font-family: "Pretendard-Regular";
  width: 100%;
  height: 50vh;
  display: flex;
  flex-wrap: wrap;
}
.Work .Work_list .Work_item {
  margin: 10px;
  width: 20%;
  border: 1px solid #ccc;
  border-radius: 15px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}
.Work .Work_list .Work_item p {
  transition: all 0.3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  font-weight: 600;
  font-size: 18px;
  letter-spacing: 1px;
}
.Work .Work_list .Work_item p span {
  font-size: 12px;
}
.Work .Work_list .Work_item:hover::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.Work .Work_list .Work_item:hover p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  color: #fff;
}
.Work .Work_list .Work_item:nth-child(1) {
  background: url("../img/Main_img.png") no-repeat;
  background-size: cover;
}
.Work .Work_list .Work_item:nth-child(2) {
  background: url("../img/otua.png") no-repeat;
  background-size: cover;
}
.Work .Work_list .Work_item:nth-child(3) {
  background: url("../img/sbox.png") no-repeat;
  background-size: cover;
}
.Work .Work_list .Work_item:nth-child(4) {
  background: url("../img/raon.png") no-repeat;
  background-size: cover;
}
.Work .Work_list .Work_item:nth-child(5) {
  background: url("../img/otuastudy.png") no-repeat;
  background-size: cover;
}
.Work .Work_list .Work_item:nth-child(6) {
  background: url("../img/cross.png") no-repeat;
  background-size: cover;
}
.Work .modal__wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.64);
  z-index: 98;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-flow: wrap;
  opacity: 0;
  pointer-events: none;
  transition: all 0.25s ease;
}
@media (min-width: 48rem) {
  .Work .modal__wrap {
    align-items: center;
  }
}
.Work .modal__wrap.is-open {
  opacity: 1;
  pointer-events: auto;
  transition: all 0.25s ease;
}
.Work .modal__wrap.is-open .modal__card {
  transform: translate3d(0, 0, 0);
  transition: all 0.5s cubic-bezier(0.4, 0, 1, 1);
}
@media (min-width: 48rem) {
  .Work .modal__wrap.is-open .modal__card {
    transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  }
}
.Work .modal__wrap .modal__card {
  font-family: "Pretendard-Regular";
  padding: 20px;
  height: 400px;
  display: block;
  overflow-y: scroll;
  background: #ffffff;
  box-shadow: 0 8px 24px 0 rgba(0, 0, 0, 0.15), 0 0 1px 0 rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  transform: translate3d(0, 100%, 0);
  transition: all 0.5s cubic-bezier(0, 0, 0.2, 1);
  position: relative;
}
@media (min-width: 48rem) {
  .Work .modal__wrap .modal__card {
    transform: translate3d(0, -25%, 0);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }
}
.Work .modal__wrap .modal__card header {
  min-width: 750px;
  display: flex;
}
.Work .modal__wrap .modal__card header div {
  width: 100%;
}
.Work .modal__wrap .modal__card span {
  padding-top: 10px;
  display: block;
  font-size: 14px;
  color: #979797;
  font-weight: 500;
}
.Work .modal__wrap .modal__card p {
  margin-top: 5px;
}
.Work .modal__wrap .modal__card .prev_btn {
  position: absolute;
  right: 30px;
  top: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  text-align: center;
  line-height: 30px;
  color: #444444;
  font-weight: 600;
}
.Work .modal__wrap .modal__card img {
  margin-top: 10px;
  height: 600px;
  width: 100%;
  overflow-y: auto;
}
.Work .modal__wrap .modal__card .Work_viewBtns {
  width: 100%;
  margin-top: 50px;
  background-color: #fff;
  border: none;
}
.Work .modal__wrap .modal__card .Work_viewBtns a {
  background-color: #fff;
  transition: all 0.3s;
  padding: 5px 40px;
  border: 1px solid #000;
  font-size: 17px;
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
.Work .modal__wrap .modal__card .Work_viewBtns a:hover {
  background-color: #000;
  color: #fff;
}

.footer {
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: center;
  background-color: #444444;
  border-top: 1px solid #ccc;
  font-family: "Pretendard-Regular";
}
.footer .footer_list {
  margin-top: 2rem;
}
.footer .footer_list .footer_ikon_list {
  display: flex;
  justify-content: center;
}
.footer .footer_list .footer_ikon_list div {
  margin-left: 1rem;
  display: inline-block;
  padding: 2px;
  border-radius: 2rem;
  cursor: pointer;
}
.footer .footer_list .footer_ikon_list div img {
  width: 3rem;
  height: 3rem;
}
.footer .footer_list p {
  margin-top: 1rem;
  font-size: 14px;
  color: #fff;
}